<template>
<div class="couponUse">
    <topNavAct title="卡券核销" url="/" :act="actions"></topNavAct>
    <div class="warp">
        <div class="orderSta clearfix">
            <div class="Num">营业点：
                <span style="color:red">{{ couponPs.pcname?couponPs.pcname:"全店" }}</span>
            </div>
            <div class="sta">本日累计：
                <span style="color:red">{{hasCount}}</span> 条
            </div>
        </div>

        <div class="infoList" v-if="coupon">
            <div class="info">
                <ul class="infoContent twocol">
                    <li style="width:70%">券号:
                        <span class="infoVal">{{coupon.couponNo}}</span>
                    </li>
                    <li style="width:30%">状态:
                        <span class="infoVal fsti">{{coupon.sta}}</span>
                    </li>
                    <li>名称:
                        <span class="infoVal">{{coupon.couponName}}</span>
                    </li>

                    <li>代码:
                        <span class="infoVal">{{coupon.couponCode}}</span>
                    </li>
                    <li>类别:
                        <span class="infoVal">{{coupon.couponTyDes}}</span>
                    </li>
                    <li>会员卡:
                        <span class="infoVal">{{niming(coupon.cardNo)}}</span>
                    </li>
                    <li style="color:red">面值:
                        <span class="infoVal" v-if="coupon.couponType=='RF'">{{coupon.presentValue}}折</span>
                        <span class="infoVal" v-else>￥{{coupon.presentValue}}</span>
                    </li>
                    <li style="color:red">入账:
                        <span class="infoVal">{{coupon.support_checkIn}}</span>
                    </li>

                    <li>有效日期:
                        <span class="infoVal">{{coupon.validFromDate}}</span>
                    </li>
                    <li>截止日期:
                        <span class="infoVal"> {{coupon.validToDate}}</span>
                    </li>
                    <li>适用日期:
                        <span class="infoVal">{{coupon.hotelDateSet}}</span>
                    </li>
                    <li class="p100">适用营业点:
                        <span class="infoVal" v-if="coupon.couponPccodeList">
                            <template v-for="(item,index) in coupon.couponPccodeList">
                                <template v-if="index>0">,</template>
                             {{item.pccode}}
                             </template>
                          </span>
                    </li>
                    <li class="p100">描述:
                        <span class="infoVal">{{coupon.couponDes}}</span>
                    </li>
                    <li class="p100">备注:
                        <span class="infoVal">{{coupon.remark}}</span>
                    </li>
                </ul>
            </div>
        </div>
    </div>
    <nothing :condition="empty" :dis="edis"></nothing>
    <div class="fixL">
        <ul>
            <li @click='getbarcode' :class="{p100:!btnv}">扫码</li>
            <li @click='use' v-if="btnv">核销</li>
        </ul>
    </div>
</div>
</template>

<script>
import {
    Toast,
    MessageBox
} from 'mint-ui';
import moment from "moment";
import nothing from '../common/nothing'
import topNavAct from "../common/topNavAct";
export default {
    data() {
        return {
            coupon: null,
            btnv: false,
            empty: false,
            edis: "",
            couponPs: !!localStorage.ps ?
                JSON.parse(localStorage.ps) : {
                    accountLogUser: "",
                    pccode: null,
                    pcname: null,
                },
            searchSumOption: {
                pccode: null,
                userSource: 'YPOS',
                beginDate: moment().format("YYYY-MM-DD"),
                endDate: moment().add(1, 'days').format("YYYY-MM-DD"),
            },
            hasCount: 0,
            actions: [{
                name: "历史查询",
                method: () => {
                    this.nav("/CouponSearch");
                }
            }]
        }
    },
    mounted() {
        this.searchSumOption.pccode = this.couponPs.pccode;
        if (this.$route.query.couponNo) {
            this.getDetail(this.$route.query.couponNo)
        } else {
            this.empty = true;
            this.edis = "请先扫描优惠券";
        }
        this.getSum();
        //this.getDetail("CO1236490000649547");
    },
    methods: {
        nav(v) {
            this.$router.push(v + "?redirect=/CouponUse" + (this.$route.query.couponNo ? encodeURI("?couponNo=" + this.$route.query.couponNo) : ""));
        },
        getDetail(v) {
            this.$http.post("/pos/posUnion/getCouponByNo", {
                couponNo: v,
                pccode: this.couponPs.pccode,
            }).then(res => {
                if (res.data.errorCode == 0) {
                    this.empty = false;
                    if (res.data.result.length > 0) {
                        this.coupon = res.data.result[0];
                        if (this.coupon.sta == "I") {
                            this.btnv = true;
                        } else {
                            this.msg("当前优惠券不可核销");
                        }
                    } else {
                        this.coupon = null;
                        this.empty = true;
                        this.edis = "优惠券不可用";
                        this.btnv = false;
                        this.msg("没有找到优惠券");
                    }
                } else {
                    this.coupon = null;
                    this.empty = true;
                    this.edis = "优惠券不可用";
                    this.btnv = false;
                    this.msg(res.data.errorMsg);
                }
            })
        },
        getSum() {
            this.$http.post("/pos/posUnion/findCouponSum", this.searchSumOption).then(res => {
                if (res.data.errorCode == 0) {
                    var count = 0;
                    if (res.data.result) {
                        for (var i = 0; i < res.data.result.length; i++) {
                            count += parseInt(res.data.result[i].count);
                        };
                    }
                    this.hasCount = count;
                } else {
                    this.msg(res.data.errorMsg);
                }
            })
        },
        use() {
            if (this.coupon.couponPccodeList && this.coupon.couponPccodeList.length > 0) {
                if (!this.couponPs.pccode) {
                    this.msg('此优惠券需要指定营业点');
                    return;
                }
                let find = false;
                for (let i = 0; i < this.coupon.couponPccodeList.length; i++) {
                    var ps = this.coupon.couponPccodeList[i].pccode.split(',');
                    if (ps.indexOf(this.couponPs.pccode) >= 0) {
                        find = true;
                        break;
                    }
                }
                if (!find) {
                    this.msg('当前营业点不能核销');
                    return;
                }
            }
            MessageBox.confirm("确定核销？").then(action => {
                this.$http.post("/pos/posUnion/onlineUseNoAccountCoupon", {
                    couponNo: this.coupon.couponNo,
                    pccode: this.couponPs.pccode,
                    userSource: 'YPOS',
                    checkIn: this.coupon.support_checkIn,
                }).then(res => {
                    if (res.data.errorCode == 0) {
                        this.msg("使用成功");
                        this.coupon.sta = "O";
                        this.btnv = false;
                        this.getSum();
                    } else {
                        this.msg(res.data.errorMsg);
                    }
                })
            });
        },
        getbarcode() {
            ScanData(this.scanSucceed, null);
        },
        scanSucceed(value) {
            if (value != null && value != "") {
                if (value.length < 60) {
                    this.getDetail(value);
                } else {
                    this.msg("不是数据");
                }
            } else {
                this.msg("扫描数据为空");
            }
        },
        niming(v) {
            if (v && v.length > 4) {
                return v.substr(0, 2) + "***" + v.substr(v.length - 2);
            }
            return v;
        },
        msg(msg) {
            Toast({
                message: msg,
                position: 'bottom',
                duration: 3000
            });
        }
    },
    components: {
        nothing,
        topNavAct
    }
}
</script>

<style lang="less">
.couponUse {
    .warp {
        margin: 10px auto;
        background: #F6F8F9;

        .orderSta {
            background-color: #F6F8F9;
            height: 32px;
            line-height: 26px;
            color: #333;

            .Num {
                float: left;
                margin-left: 8px;
            }

            .sta {
                float: right;
                margin-right: 8px;

            }
        }

        .infoList {
            margin: 0 10px;
            background: #fff;
            box-shadow: 0 -2px 20px rgba(0, 0, 0, 0.15);
            border-radius: 4px;

            .info {
                padding: 10px 0;

                .infoContent {
                    margin: 0 20px;
                    padding: 10px 0;
                    clear: both;

                    .p100 {
                        clear: both;
                        width: 100% !important;
                    }

                    li {
                        margin: 2px auto;
                        line-height: 20px;
                        text-align: left;

                        .infoVal {
                            margin-left: 8px;

                            a {
                                color: #000;
                            }
                        }

                        .paiF {
                            height: 32px;
                            width: 70px;
                            float: right;
                            text-align: center;
                            line-height: 32px;
                            background-color: #f4f4f4;
                            position: relative;
                            top: -15px;
                            border: 1px solid #ddd;
                            border-radius: 4px;
                        }
                    }

                    &.twocol {
                        overflow: hidden;

                        li {
                            width: 50%;
                            float: left;
                        }
                    }
                }
            }
        }
    }

    .fixL {
        width: 78.7%;
        margin-top: 2rem;
        display: inline-block;

        ul {
            margin-top: 0.4rem;
            margin-bottom: 0.4rem;

            li {
                width: 47.2%;
                height: 1.2rem;
                background: #ff8902;
                border-radius: 0.1867rem;
                text-align: center;
                line-height: 1.2rem;
                display: inline-block;
                margin-right: 0.1333rem;
                color: #fff;
            }

            .p100 {
                width: 100%;

            }
        }
    }

    .fsti {

        color: #FFFFFF;
        font-weight: bold;
        font-size: 16px;
        border-radius: 12px;
        display: inline-block;
        width: 22px;
        height: 22px;
        text-align: center;
        background: #646F83;
        vertical-align: middle;
        line-height: 22px;

    }
}
</style>



