<template>
<div class="topHeaderAct">
    <mt-header :title="title" fixed :style="{backgroundColor:bgcolor}">
        <i class="mintui mintui-back" @click="geturl()" slot="left"></i>
        <div class="mint-header-button is-right" slot="right">
            <button class="mint-button mint-button--default mint-button--normal"  @click="sheetVisible=true" >
                <span class="mint-button-icon"><i class="iconfont icon-menu1"></i></span>
            </button>
        </div>
    </mt-header>
    <actionsheet :actions="actions" v-model="sheetVisible"></actionsheet>
</div>
</template>
<script>
import {
    Actionsheet
} from "mint-ui";
export default {
    props: {
        back: {
            default: true
        },
        title: {
            default: ""
        },
        url: {
            default: "/"
        },
        selfMethod: {
            default: ""
        },
        act: {
            default: []
        },
        bgcolor: {
            default: "#00487C"
        }
    },
    data() {
        return {
            sheetVisible: false,
            actions: [],
        };
    },
    mounted() {
        this.actions = this.act;
    },
    methods: {
        geturl() {
            if (this.selfMethod == "") {
                if (this.$route.query.redirect != null) {
                    return this.$router.push(this.$route.query.redirect);
                }
                return this.$router.push(this.url);
            } else {
                this.$emit('leftClick');
            }
        }
    },
    computed: {

    },
    components: {
        actionsheet: Actionsheet
    }
};
</script>

<style lang="less" scoped>
.topHeaderAct {
    height: 1.04rem;
    .mint-header {
        color: #fff;
        font-size: 16px;
        top: -1px;
        padding: 0;
        z-index: 100;

    }
    .mintui-back {
        line-height: 0.853333rem;
        padding: 0.2rem;
    }
    .mint-button {
        padding: 0 0.2rem;
    }
}
</style>



